<template>
  <div v-if="!isLogin" class="py-6 rtl" style="direction: rtl">
    <v-row justify="center" class="px-6">
      <div
        style="
          padding: 15px;
          padding-top: 2px;
          margin-right: 0;
          margin-top: 25px;
          margin-bottom: 7px;
          position: relative;
          min-height: 220px;
          max-width: 323px;
          width: 100%;
          background-color: #fff;
          border: 1px solid grey;
          border-radius: 2px;
        "
        class="page"
      >
        <v-row justify="center" class="py-3"> ورود کاربر </v-row>
        <v-form lazy-validation ref="form">
          <v-container>
            <v-row>
              <v-col cols="12">
                <v-text-field
                  v-model="username"
                  :rules="nameRules"
                  label="نام کاربری"
                  required
                ></v-text-field>
              </v-col>

              <v-col cols="12">
                <v-text-field
                  v-model="password"
                  :rules="nameRules"
                  label="رمز عبور"
                  required
                ></v-text-field>
              </v-col>
            </v-row>
            <span class="red--text">
              {{ errorrMessage }}
            </span>
          </v-container>
        </v-form>

        <v-row justify="center" class="py-3">
          <v-btn color="green" dark class="px-4 mt-3" @click="validate"
            >ورود</v-btn
          >
        </v-row>
      </div>
    </v-row>
  </div>
  <v-row v-else-if="isScanning" class="h-100 pa-6" justify="center">
    <div>
      <v-row justify="end">
        <v-btn to="/admin" class="ma-4 px-6" outlined color="#00559b"
          >مشاهده لیست جعبه ها</v-btn
        >
      </v-row>
      <v-row justify="center" class="px-3">
        <div
          class="rounded-xl mt-4"
          style="
            width: 100%;
            height: 70vh;
            border: 1px solid grey;
            overflow: hidden;
          "
        >
          <qrcode-stream @decode="onDecode" @init="onInit"></qrcode-stream>
        </div>
      </v-row>
      <v-row justify="center">
        <h3>... scaning</h3>
      </v-row>
      <v-row justify="center">
        <p v-if="decodedContent">نتیجه: {{ decodedContent }}</p>
      </v-row>
    </div>
  </v-row>
  <v-row
    v-else-if="isLogin && isLoading"
    class="h-100"
    justify="center"
    align="center"
  >
    <v-progress-circular indeterminate color="primary"></v-progress-circular>
  </v-row>
  <div
    v-else-if="isLogin && box.Status"
    class="py-6 rtl"
    style="direction: rtl"
  >
    <v-row justify="center">
      <span
        :style="{
          color: box.dontsend
            ? 'red'
            : box.Status == 'ready'
            ? '#003ac3'
            : box.Status == 'delivered_to_driver'
            ? 'green'
            : box.Status == 'delivered_to_customer'
            ? 'green'
            : 'red',
        }"
      >
        {{
          box.dontsend
            ? "!!!ارسال نشود"
            : box.Status == "ready"
            ? "در انتظار تحویل به راننده"
            : box.Status == "delivered_to_driver"
            ? "به راننده تحویل داده شد"
            : box.Status == "delivered_to_customer"
            ? "تحویل شده به مشتری"
            : "مرجوع شده"
        }}
      </span>
    </v-row>
    <v-row justify="center" class="px-6" v-if="box && box.Status">
      <div class="mb-12" style="max-width: 323px; width: 100%">
        <div
          style="
            padding: 15px;
            padding-top: 2px;
            margin-right: 0;
            margin-top: 25px;
            margin-bottom: 7px;
            position: relative;
            min-height: 220px;
            max-width: 323px;
            width: 100%;
            background-color: #fff;
            border: 1px solid;
            border-radius: 2px;
          "
          :style="{ borderColor: box.dontsend ? 'red' : 'grey' }"
          class="page"
        >
          <v-row justify="center" class="pa-3">
            <span style="width: 100%; text-align: center">
              <b
                style="border: 1px solid; padding: 0 4px"
                :style="{ borderColor: box.dontsend ? 'red' : '#878787' }"
              >
                {{ box.DriverBoxCount }}
              </b>
              <b
                v-if="box.ShamsiDate"
                style="border: 1px solid #878787; padding: 0 4px"
                :style="{ borderColor: box.dontsend ? 'red' : '#878787' }"
              >
                {{ box.ShamsiDate.slice(5) }}
              </b>
              <b
                style="border: 1px solid #878787; padding: 0 4px"
                :style="{ borderColor: box.dontsend ? 'red' : '#878787' }"
              >
                جعبه شماره
                {{ box.index }}
                از
                {{ box.length }}
              </b>
            </span>
          </v-row>
          <v-row
            style="
              align-items: center;
              justify-content: center;
              background: #fff;
              padding-right: 16px;
              padding-left: 16px;
              min-height: 170px;
            "
            :style="{
              background:
                'url(' +
                (box.DriverCode == 'محمد بابا گلي' || box.DriverCode == '01'
                  ? '1.png'
                  : box.DriverCode == 'فروشگاه گلستان'
                  ? 'گلستان.png'
                  : box.DriverCode == 'فروشگاه كوه نور'
                  ? '111.png'
                  : box.DriverCode == 'فروشگاه جردن'
                  ? 'جردن.png'
                  : box.DriverCode == 'فروشگاه سام سنتر'
                  ? '222.png'
                  : box.DriverCode == 'منوچهر اشرفي' || box.DriverCode == '02'
                  ? '2.png'
                  : box.DriverCode == 'فريد باباگلي' || box.DriverCode == '03'
                  ? '3.png'
                  : box.DriverCode == 'محمد براتي' || box.DriverCode == '05'
                  ? '5.png'
                  : box.DriverCode == 'علي محمدي' || box.DriverCode == '06'
                  ? '6.png'
                  : box.DriverCode == 'جواد پوربجنوردي' ||
                    box.DriverCode == '07'
                  ? '7.png'
                  : box.DriverCode == '08'
                  ? '8.png'
                  : '4.png') +
                ')',
            }"
          >
            <div style="width: 100%; height: fit-content">
              <v-row
                v-for="(rowOrderLabe, g) in box.Items"
                :key="'rowOrderLabe' + g"
                :style="{ height: box.Items.length >= 11 ? '15px' : '22px' }"
              >
                <span
                  :style="{
                    fontSize: '15px',
                    lineHeight:
                      box.Items.length >= 9 && box.Items.length < 11
                        ? '18px'
                        : box.Items.length >= 11 && '16px',
                  }"
                >
                  {{ rowOrderLabe.name }}
                </span>
                <v-spacer></v-spacer>
                <span
                  class="mr-4"
                  :style="{
                    fontSize: box.Items.length > 6 ? '12px' : '16px',
                    lineHeight: box.Items.length >= 9 && '18px',
                  }"
                  >{{ rowOrderLabe.quanity }}</span
                >
              </v-row>
            </div>
          </v-row>
          <v-row
            style="
              border-top: 1px solid black;
              font-size: 12px;
              justify-content: space-between;
              align-items: center;
              flex-wrap: nowrap;
            "
            :style="{ borderColor: box.dontsend ? 'red' : 'black' }"
            class="mt-6 pl-3"
          >
            <h2 style="font-size: 20px !important">
              {{
                box.DriverCode == "محمد بابا گلي" || box.DriverCode == "01"
                  ? "01"
                  : box.DriverCode == "فروشگاه گلستان"
                  ? "گلستان"
                  : box.DriverCode == "فروشگاه كوه نور"
                  ? "کوه نور"
                  : box.DriverCode == "فروشگاه جردن"
                  ? "جردن"
                  : box.DriverCode == "فروشگاه سام سنتر"
                  ? "سام سنتر"
                  : box.DriverCode == "منوچهر اشرفي" || box.DriverCode == "02"
                  ? "02"
                  : box.DriverCode == "فريد باباگلي" || box.DriverCode == "03"
                  ? "03"
                  : box.DriverCode == "محمد براتي" || box.DriverCode == "05"
                  ? "05"
                  : box.DriverCode == "علي محمدي" || box.DriverCode == "06"
                  ? "06"
                  : box.DriverCode == "جواد پوربجنوردي" ||
                    box.DriverCode == "07"
                  ? "07"
                  : box.DriverCode == "08"
                  ? "08"
                  : "04"
              }}
            </h2>
            <div style="white-space: nowrap">
              <h2
                v-if="box.ShopName.length < 25"
                style="font-size: 23px !important; text-shadow: 1px 1px BLACK"
              >
                {{ box.ShopName }}
              </h2>
              <h3
                v-else
                style="font-size: 15px !important ; text-shadow: 1px 1px BLACK"
              >
                {{ box.ShopName }}
              </h3>
            </div>
            <div style="position: relative; text-shadow: 1px 1px BLACK">
              <v-row>
                <h2
                  style="font-size: 20px !important; text-shadow: 1px 1px BLACK"
                >
                  {{ box.index }}/{{ box.length }}
                </h2>
              </v-row>
              <v-row
                style="
                  position: absolute;
                  left: 9px;
                  bottom: -7px;
                  white-space: nowrap;
                "
              >
                {{ box.emptySize >= 0.7 ? "کوچک" : "" }}
              </v-row>
            </div>
          </v-row>
        </div>
        <v-row align="center" v-if="!box.dontsend">
          <v-checkbox
            :disabled="disabelChangeDriver"
            style="max-width: 323px"
            outlined
            v-model="box.SendByOther"
            hide-details
            @change="box.Driver2 = ''"
          >
          </v-checkbox>
          <span
            class="mt-5"
            :style="{ color: box.SendByOther ? 'black' : '#878787' }"
          >
            راننده شخص دیگری است؟
          </span>
        </v-row>
      </div>
    </v-row>
    <v-form v-if="box && !box.dontsend && box.Status">
      <v-row justify="center" class="mb-5" v-if="box.SendByOther">
        <v-select
          :disabled="disabelChangeDriver"
          style="max-width: 323px"
          :items="drivers"
          outlined
          v-model="box.Driver2"
          placeholder="انتخاب راننده"
          hide-details
        ></v-select>
      </v-row>
    </v-form>

    <v-row v-if="box && box.Status && !box.dontsend" justify="center" class="">
      <div
        class="mx-2"
        style="position: relative; width: 100%; max-width: 323px"
      >
        <v-textarea
          auto-grow
          style="max-width: 323px"
          class="w-100"
          v-model="box.Description"
          placeholder="توضیحات تکمیلی"
          outlined
          rows="1"
          prepend-inner-icon="mdi-comment"
          hide-details
        ></v-textarea>
      </div>
    </v-row>
    <v-row v-if="box && box.Status && !box.dontsend" justify="center">
      <v-btn
        class="px-12 mt-3"
        style="background: rgb(0, 142, 255); border-radius: 4px"
        dark
        @click="changeStatus(box._id, box.Status, 'isDescription')"
      >
        تحویل شد به راننده
      </v-btn>
    </v-row>
    <v-alert
      v-if="showAlert"
      style="position: fixed; right: 10px; bottom: 0"
      border="top"
      :color="alertType == 'error' ? 'red lighten-2' : 'green lighten2'"
      dark
    >
      {{ alertText }}
    </v-alert>
  </div>

