<style>
.h-100 {
  height: 100vh;
}
.v-application--is-ltr .v-text-field .v-label {
  right: 0 !important;
  left: auto !important;
}
</style>

<script>
import axios from "axios";
axios.defaults.headers.common["Token"] = "CookiBox";
axios.defaults.headers.common["Password"] = "92jngv4908ed";
axios.defaults.headers.common["Access-Control-Allow-Origin"] = "*";

export default {
  name: "qr-code",
  components: {},
  data: () => ({
    date: "",
    drivers: [
      "01",
      "02",
      "03",
      "04",
      "05",
      "06",
      "07",
      "08",
      "فروشگاه سام سنتر",
      "فروشگاه كوه نور",
      "فروشگاه گلستان",
      "فروشگاه جردن",
      "متفرقه",
    ],
    disabelChangeDriver: false,
    decodedContent: "",
    showAlert: false,
    alertType: "error",
    alertText: "",
    emptyQuery: true,
    box: {},
    username: "",
    password: "",
    loadingButton: false,
    isLogin: false,
    isLoading: true,
    isScanning: true,
    nameRules: [(v) => !!v || "فیلد اجباری"],
    errorrMessage: "",
    items: [
      { text: "آماده تحویل", value: "ready" },
      { text: "تحویل شده به راننده", value: "delivered_to_driver" },
      { text: "تحویل شده به مشتری", value: "delivered_to_customer" },
      { text: "مرجوع شده", value: "returned" },
    ],
    status: "ready",
  }),
  watch: {
    status() {
      // this.changeStatus(this.box._id, this.status);
    },
  },
  methods: {
    getBox(date, id) {
      return new Promise((resolve) => {
        let self = this;
        self.isScanning = false;
        let d = new Date(date);
        d.setDate(d.getDate()); // یک روز به تاریخ اضافه می‌کنه
        const timestamp = d.getTime(); // خروجی: زمان به میلی‌ثانیه
        axios
          .get(
            "https://172.16.16.15:3000/api/dispatch/" +
              (timestamp + 86400000) +
              "/boxes/" +
              id,
            {}
          )
          .then(function (response) {
            self.date = date;
            self.box = response.data.data;
            self.drivers = self.drivers.filter(
              (x) => x != response.data.data.DriverCode
            );
            self.disabelChangeDriver = response.data.disabelChangeDriver;
            self.isLoading = false;
            // resolve(response.data);
          })
          .catch(function (error) {
            // handle error
            console.log(error.response.data.errors[0].msg);
          });
      });
    },
    changeStatus(id, Status, isDescription) {
      return new Promise((resolve) => {
        this.isLoading = true;
        if (this.box.SendByOther && !this.box.Driver2) {
          this.showAlert = true;
          this.alertType = "error";
          this.alertText = "انتخاب راننده اجباریست!";
        } else {
          let self = this;
          self.loadingButton = true;
          let d = new Date(self.date);
          d.setDate(d.getDate() + 1); // یک روز به تاریخ اضافه می‌کنه
          const timestamp = d.getTime(); // خروجی: زمان به میلی‌ثانیه
          axios
            .put(
              "https://172.16.16.15:3000/api/dispatch/" +
                timestamp +
                "/boxes/" +
                id +
                "/delivered_to_driver",
              {
                description: self.box.Description,
                SendByOther: self.box.SendByOther,
                Driver2: self.box.Driver2,
              }
            )
            .then(function (response) {
              self.box = response.data.data;
              resolve(response.data);
              self.loadingButton = false;
              self.showAlert = true;
              self.alertType = "success";
              self.alertText = "بروزرسانی با موفقیت انجام شد";
              self.isLoading = false;
              setTimeout(() => {
                self.showAlert = false;
                self.isScanning = true;
              }, 3000);
            })
            .catch(function (error) {
              // handle error
              console.log(error.response.data.errors[0].msg);
            });
        }
      });
    },
    validate() {
      if (this.$refs.form.validate()) {
        if (this.username == "operator" && this.password == "operator") {
          localStorage.setItem("loginCookiebox", "operator");
          this.isLogin = true;
        } else {
          this.errorrMessage = "نام کاربری یا رمزعبور اشتباه است";
        }
      }
    },
    onDecode(result) {
      let findResult = result.split("&");
      this.decodedContent = findResult;
      let date = findResult[0].split("=");
      date = date[1];
      let id = findResult[1].split("=");
      id = id[1];
      this.getBox(date, id);
    },
    onInit(promise) {
      promise.catch((error) => {
        if (error.name === "NotAllowedError") {
          alert("اجازه دسترسی به دوربین داده نشد");
        } else if (error.name === "NotFoundError") {
          alert("هیچ دوربینی پیدا نشد");
        } else {
          alert("خطا: " + error.message);
        }
      });
    },
  },
  created() {
    if (localStorage.getItem("loginCookiebox") == "operator") {
      this.isLogin = true;
      // this.getBox("2025-09-27", "68d79b496de69a8af7501fb7");
    }
  },
};
</script>
